</template>

<script>
    export default {
        props: [
            'portfolios',
            'categories',
            'categoryId',
            'pagination',
            'catePaginate',
            'fetchPortfolio',
            'filterCategory',
        ],
        data: () => ({
        }),
        computed: {
            cateId: {
                get () {
                    return this.categoryId
                },
                 set (value) {
                    this.$store.commit('SetCATEGORY', value)
                }
            }
        },
        methods: {
            filterCate (value) {
                this.$store.commit('SetCATEGORY', value)
                this.filterCategory(1)
            }
        }
    }
</script>

