<template>
    <v-container>
        <h1 class="text-center">
            ປະເພດ
        </h1>
        <v-card>
            <swiper :options="swiperOption">
                <swiper-slide v-for="(category, index) in categories.slice(1)" :key="index" class="text-center">
                    <a  @click="filterCate(category._id)">
                        <div class="__a-category">
                            <v-img
                                    class="mx-auto my-2"
                                    :src=" category.image ? $server + category.image : '' "
                                    height="70px"
                                    width="70px"
                            >
                            </v-img>
                        </div>
                    </a>
                    <p :class=" category._id === categoryId ? 'current-category' : ''">{{ category.name }}</p>
                </swiper-slide>
                <div id="__swiper-pagination" class="swiper-pagination" slot="pagination"></div>
<!--                <div  class="swiper-button-prev" slot="button-prev"></div>-->
<!--                <div class="swiper-button-next" slot="button-next"></div>-->
            </swiper>
        </v-card>
    </v-container>

</template>

<script>
    export default {
        props: [
            'categoryId',
            'filterCategory',
            'categories'
        ],
        data:() => ({
            swiperOption: {
                slidesPerView: 6,
                spaceBetween: 20,
                breakpoints: {
                    576: {
                        slidesPerView: 3,
                        spaceBetween: 20
                    },
                    768: {
                        slidesPerView: 4,
                        spaceBetween: 20
                    },
                    992: {
                        slidesPerView: 6,
                        spaceBetween: 20
                    },
                    1200: {
                        slidesPerView: 6,
                        spaceBetween: 20
                    }
                },
                freeMode: true,
                // autoplay: {
                //   delay: 4000,
                //   disableOnInteraction: false
                // },
                pagination: {
                    el: ".swiper-pagination",
                    clickable: true,
                    dynamicBullets: true,
                    type: 'progressbar'
                },
                navigation: {
                    nextEl: '.swiper-button-next',
                    prevEl: '.swiper-button-prev'
                }
            }
        }),
        methods: {
            filterCate (id) {
                this.$store.commit('SetCATEGORY', id)
                this.filterCategory(1)
            }
        }
    }
</script>

