<template>
    <div>
        <div v-if="loading">
            <v-container fluid style="max-width: 1000px">
                <vue-content-loading :speed="0.8" :width="300" :height="370">
                    <rect x="0" y="0" rx="4" ry="4" width="300" height="30" />
                    <rect x="30" y="45" rx="4" ry="4" width="120" height="10" />
                    <rect x="15" y="60" rx="4" ry="4" width="150" height="20" />
                    <rect x="25" y="85" rx="4" ry="4" width="60" height="10" />
                    <rect x="95" y="85" rx="4" ry="4" width="60" height="10" />
                    <rect x="170" y="40" rx="4" ry="4" width="120" height="80" />
                    <rect x="0" y="130" rx="4" ry="4" width="300" height="30" />
                    <rect x="80" y="165" rx="4" ry="4" width="130" height="15" />
                    <rect x="20" y="185" rx="4" ry="4" width="260" height="25" />
                    <rect x="0" y="220" rx="4" ry="4" width="70" height="15" />
                    <rect x="0" y="245" rx="4" ry="4" width="75" height="90" />
                    <rect x="80" y="245" rx="4" ry="4" width="75" height="90" />
                    <rect x="160" y="245" rx="4" ry="4" width="75" height="90" />
                    <rect x="160" y="245" rx="4" ry="4" width="75" height="90" />
                    <rect x="120" y="340" rx="4" ry="4" width="75" height="15" />
                </vue-content-loading>
            </v-container>
        </div>
        <div v-else>
            <app-first></app-first>
            <v-container fluid style="max-width: 1000px">
                <app-search
                ></app-search>
                <app-category
                        :categoryId="categoryId"
                        :categories="categories"
                        :filterCategory="filterCate"
                >
                </app-category>
                <app-portfolio
                        :portfolios="portfolios"
                        :categories="categories"
                        :pagination="pagination"
                        :categoryId="categoryId"
                        :fetchPortfolio="fetchPort"
                        :filterCategory="filterCate"
                        :catePaginate="catePaginate"
                        :showFilter="showFilter"
                ></app-portfolio>
            </v-container>
        </div>
    </div>
</template>

<script>
    import appFirst from '../../components/home/First'
    import appSearch from '../../components/home/Search'
    import appCategory from '../../components/home/Category'
    import appPortfolio from '../../components/home/Portfolio'
    import { mapActions, mapMutations ,mapGetters } from 'vuex'
    import VueContentLoading from 'vue-content-loading';
    export default {
        components: {
            appFirst,
            appSearch,
            appCategory,
            appPortfolio,
            VueContentLoading,
        },
        data: () => ({
            showFilter: true,
            categories: [],
            loading: true
        }),
        computed: {
            ...mapGetters([
                'portfolios',
                'pagination',
                'categoryId',
                'catePaginate'
            ])
        },
        methods: {
            ...mapActions([
                'fetchPortfolios',
                'filterCategory'
            ]),
            ...mapMutations([
               'PushPORTFOLIO'
            ]),
            async fetchPort () {
                await this.fetchPortfolios(this.pagination.page)
            },
            async filterCate (page) {
                if(this.categoryId === 'all') {
                    await this.fetchPortfolios(page)
                }
                else {
                    await this.filterCategory(page)
                }
            },
            async fetchCategories () {
                try {
                    const results = await this.$axios.get('category/getall')
                    const categories = results.data.categories
                    categories.unshift({ name: 'ທັງໝົດ', _id: 'all' })
                    this.categories = categories
                } catch (e) {
                    throw e
                }
            }
        },
       async created () {
            const queryCate = this.$route.query.by_category
            if(!queryCate){
               await this.fetchPortfolios(1)
            }
            if(queryCate) {
                this.$store.commit('SetCATEGORY', queryCate)
               await this.filterCategory(1)
            }
            await this.fetchCategories()
            setTimeout(() => {
                this.loading = false
            })
        },
        mounted () {
            this.$io.on('postPortfolio', () =>  this.fetchPortfolios(1))
            this.$io.on('updatePortfolio', () =>  this.fetchPortfolios(1))
            this.$io.on('deletePortfolio', () =>  this.fetchPortfolios(1))
        }
    }
</script>
